.paymentHolder{
    height: 145px;
    width: 145px;    
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    /* background: linear-gradient(to right, rgba(170,170,170,0), rgba(255,255,255,0.2)); */
    background: var(--bc-color);

    /* opacity: 0.5; */
}

.paymentHolder::before{
    content:'';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(48, 48, 48, 0.702);
    border-radius: 15px;
    transition: all 0.5s ease;
}

.paymentHolder.highlight::before{
    background-color: rgba(255,255,255, 0.102);    
    /* opacity: 0.7; */
    
}

.paymentHolder img{
    max-height: 50%;
    max-width: 90%;
}

.firstLine, .secondLine{
    display: flex;
    gap: 12px;
}

.firstLine{
    margin-bottom: 11px;
}

.secondLine{
    margin-bottom: 40px;
}


.secondLine .paymentHolder::before{
    background: linear-gradient(to bottom, #303030b3,#222222); /* Gradient colors */

}
.secondLine .paymentHolder.highlight::before{
    background-color: rgba(255,255,255, 0.302);    
    /* opacity: 0.7; */
    
}
.spending-section{
    border-radius: 24px;
    padding: 13px;
    background: linear-gradient(to top, #252525,#1f1f1f); /* Gradient colors */
    /* -webkit-background-clip: text; linear-gradient(to top, #252525, #1f1f1f)
    -webkit-text-fill-color: transparent; */
}

.spending-section .cardContent p{
    max-width: 450px;
}




@media(1200px<= width <=1500px){
    .paymentHolder {
        width: 110px;
        height: 110px;
        min-height: 100px;
    }

    .spending-section .cardContent p{
        width: 60%;
    }
}

@media(1200px<= width){
    .paymentHolder img{
        max-width: 60%;
    }

    .spending-section{
        height: fit-content;
    }

    .spending-section .cardContent{
        padding: 20px;
    }
}


@media(max-width:800px){
    .spending-section .cardContent{
        padding: 7px;
    }

    .paymentHolder{
        height: 50px;
        padding: 10px;
    }
}