.yield-section {
  background: url("../assets/images/SolutionCardThreeBg.png");
  background-size: cover;
  background-position: top right;
  background-repeat: no-repeat;
  display: flex;
  align-items: flex-end;
  border-radius: 24px;
  overflow: hidden;
}

.fourDiv {
  position: relative;
}

.divOne {
  z-index: 2;
  opacity: 0.1;
}

.rectangle {
  position: absolute;
  top: 40%;
  left: 60%;
  transform: translate(-50%, -40%);
  z-index: 3;
  opacity: 0.2;
  max-width: 50%;
}

.rectLogo {
  position: absolute;
  top: 45%;
  left: 60%;
  transform: translate(-50%, -40%);
}

.yield-section .divOne {
  /* transform: translate(10%, -15%);
  opacity: 1;
  transition: all 2s ease;
  transition-delay: 1s; */
  transform: translate(10%, -15%);
  opacity: 1;
  transition: all 2s ease;
}

.yield-section:hover .divOne{
  animation: divOne 2s ease;
  
}

@keyframes divOne {
  0%{
    transform: translate(10%, -15%);
  opacity: 1;


  }
  33.33%{
    transform: translate(10%, 0);
    opacity: 0.1;
  }
  66.66%{
    transform: translate(10%, 0);
    opacity: 0.1;

  }
  100%{
    transform: translate(10%, -15%);
  opacity: 1;
  

  }
  
}

.yield-section .rectangle {
  transform: translate(-50%, -50%);
  opacity: 1;
  transition: all 1s ease;
}

.yield-section:hover .rectangle{
  animation: rectangle 2s ease;
}

@keyframes rectangle {
  0%{
    transform: translate(-50%, -50%);
  opacity: 1;
  /* animation-delay: 1s; */

  }
  25%{
    transform: translate(-50%, -50%);
  opacity: 1;
  /* animation-delay: 1s; */

  }
  50%{
    transform: translate(-50%, -25%);
    opacity: 0.1;
  /* animation-delay: 1s; */

  }
  100%{
    transform: translate(-50%, -50%);
  opacity: 1;
  }
  
}

.yield-section .rectLogo {
  transform: translate(-50%, -70%);
  opacity: 1;
  transition: all 1s ease;
}

.yield-section:hover .rectLogo{
  animation: rectLogo 2s ease;
}

@keyframes rectLogo {
  0%{
    transform: translate(-50%, -70%);
    opacity: 1;
  }
  25%{
    transform: translate(-50%, -70%);
    opacity: 1;
  }
  50%{
    transform: translate(-50%, 0%);
    opacity: 0.1;

  }
  100%{
    transform: translate(-50%, -70%);
    opacity: 1;
  }
  
}


.yield-section .cardText {
  padding: 40px;
}

.yield-section .cardContent {
  height: -webkit-fill-available;
}

.yield-section .mainImage {
  max-width: 100%;
  object-fit: contain;
}

.yield-section .imageContainer {
  width: 100%;
  object-fit: contain;
}

.divOne {
  max-width: 100%;
  transform: translate(10%, 10%);
}

.yield-section .fourDiv img {
}

@media (max-width: 800px) {
  .yield-section {
    padding: 0;
  }

  .yield-section .cardContent {
    padding: 0;
  }

  .yield-section .rectLogo {
    max-width: 70px;
  }
}

@media (1200px <= width) {
  .yield-section {
    transform: translateY(-20px);
    margin-bottom: 40px;
  }

  .imageContainer {
    height: 250px;
  }

  .yield-section .cardContent {
    padding: 0;
  }

  .divOne {
    transform: translate(10%, 0);
  }
}

@media (1501px <= width) {
  .yield-section {
    transform: translateY(-90px);
  }

  .imageContainer {
    height: 350px;
  }
}


