/* Swap Section Styling */
.fuse-section {
    padding-top: 80px;
    padding-right: 44px;
    padding-left: 44px;
    padding-bottom: 44px;
    background: radial-gradient(circle at top left, rgba(23, 23, 23, 1) 0%, rgba(255, 255, 255, 0.05) 50%, rgba(23, 23, 23, 1) 100%);
    color: #fff;
    border-radius: 24px;
    gap: 2rem;
    /* min-height: 650px; */
    position: relative;
    overflow: hidden;
  }

  .cardContent h3{
    font-family: 'Outfit', sans-serif;
    font-size: 44px;
    line-height: 100%;
    letter-spacing: 2%;
    font-weight: normal;
    text-align: left;
    margin-top: 0;
    margin-bottom: 24px;
  }
  .fuse-section .cardContent{
    /* padding-bottom: 40px; */
  }

  .fuse-section .cardContent h3{
    max-width: 400px;
  }    

  .cardContent p{
    font-family: 'Outfit', sans-serif;
    font-size:20px;
    font-weight: 300;
    line-height: 28px;
    text-align: left;
    color: rgba(255, 255, 255, 0.75);
    margin: 0;
  }

  .fuse-section .cardContent p{
    max-width: 80%;
  }

  .imageHolder{
    display: flex;
    margin-bottom: 60px;
  }

  .imageHolder img.main{
    max-height:30vw;
    max-width: 100%;
    border-top-left-radius: 22px;
    border-top-right-radius: 22px;    
  }

  .coinsCol{
    display: flex;
    flex-direction: column;
    gap: 55px;
    margin-right: 34px;
  }

  .usdcCol{
    display: flex;
    flex-direction: column;
    gap: 55px;
    position: relative;
  }

  .usdcTwo{
    margin-left: 125px;
    margin-top: 5px;
  }

  .usdcCol img{
    /* height: 41px;
    width: 41px; */
    z-index: 2;
  }

  .usdcOne img{
    height: 42px;
    width: 42px;
  }

  .usdcTwo img{
    height: 41px;
    width: 41px;
  }
  
  .usdcThree img{
    height: 42px;
    width: 42px;
    margin-top: 4px;
  }

  .dogHolder{
    display: flex;
    align-items: center;
    margin-left: 100px;
  }

  .fuse-section:hover .dogHolder{
    animation: dogHolder 2s ease;
  }

  @keyframes dogHolder {
    0%{
      margin-left: 100px;
    }
    50%{
      margin-left: 140px;
    }
    100%{
      margin-left: 100px;
    }
    
  }

  .dog .dogImg{
    height: 100%;
    max-height: 62px;
    /* max-height: 93px; */
    width: auto;
    /* margin-left: 120px; */
    /* z-index: 2; */
    transition: all 2s ease;

  }

  .usdcOne,
  .usdcTwo, 
  .usdcThree{
    position: relative;
    overflow: visible;
    display: flex;
    justify-content: start;
  }

  .usdcOne::before,
  .usdcThree::before,
  .usdcTwo::before{
    content: '';
    height: 1px;
    width: 125px;
    position: absolute;
    background-color: grey;
    z-index: 1;
    clip-path: inset( 0 100% 0 0);
    /* transition: clip-path 2s ease-out; */
    }

  .usdcThree::before{
    /* top: -70%;
    transform: rotate(-40deg);
    right: 19px; */
    top: -70%;
    transform: rotate(-40deg);
    left: 50%;

  }

  .usdcOne::before{
    /* top: 170%;
    transform: rotate(40deg);
    right: 19px; */
    top: 170%;
    transform: rotate(40deg);
    left: 50%;

  }

  .usdcTwo::before{
    top:50%;
    left: 100%;
  }

  .fuse-section:hover .usdcOne::before,
  .fuse-section:hover .usdcTwo::before,
  .fuse-section:hover .usdcThree::before{
    animation: leftToRight 2s ease 0s 1;
    clip-path: inset( 0 0 0 0);
  }

  .fuse-section .dog .dogImg{
    max-height: 93px;

  }

  .fuse-section:hover .dog .dogImg{
    /* max-height: 93px; */
    animation: dogImage 2s ease;
  }

  @keyframes dogImage {
    0%{
    max-height: 93px;
    }
    50%{
    max-height: 62px;
    }
    100%{
    max-height: 93px;
    }
    
  }

  .fuse-section .dog .dogBg{
    height: 200%;
    width: 200%;
    left: -50%;
    top: -50%;
  }

  @keyframes dogBg {
    0%{
      height: 200%;
    width: 200%;
    left: -50%;
    top: -50%;
    }
    50%{
      height: 100%;
      width: 100%;
      left: 0%;
      top: 0%;
    }
    100%{
      height: 200%;
    width: 200%;
    left: -50%;
    top: -50%;
    }
    
  }
  .fuse-section:hover .dog .dogBg{
    /* height: 200%;
    width: 200%;
    left: -50%;
    top: -50%; */
    animation: dogBg 2s ease;
  }

  .fuse-section:hover .dogText{
    /* font-size: 38px; */
    /* margin-left: 12px; */
    animation: dogText 2s ease;
  }

  @keyframes dogText {
    0%{
      font-size: 38px;
      margin-left: 25px;
    }
    50%{
      font-size: 25px;
      margin-left: 25px;
    }
    100%{
      font-size: 38px;
      margin-left: 25px;
    }
  }

  .dogText{
    font-size: 25px;
    /* font-size: 38px; */
    line-height: 66px;
    font-weight: 500;
    margin-left: 16px;
    transition: all 2s ease;
    font-size: 38px;
    margin-left: 25px;
    /* margin-left: 25px; */
  }

