@keyframes growAndFade {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}

.dog{
  position: relative;
  display: inline-block;
}

.dogBg{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  height: 100%;
  width: 100%;
  transition: all 3s ease;
}

.dogImg{
  position: relative;
  display: block;
  z-index: 3;
}

.circle {
  position: absolute;
  border-radius: 50%; 
  
  transition: opacity 0.5s ease; 
  max-width: auto;
  max-height: auto;
}

/* Define the size and positioning of each circle */
.circle-1 {
  top: 50%;
  left: 50%;
  width: 100px; /* Adjust size as needed */
  height: 100px; /* Adjust size as needed */
  border: 1px solid rgba(80,80,80, 0.5); /* Circle color */
  transform: translate(-50%, -50%); /* Center the circle */
}

.circle-2 {
  top: 50%;
  left: 50%;
  width: 150px; /* Adjust size as needed */
  height: 150px; /* Adjust size as needed */
  border: 1px solid rgba(80,80,80, 0.4);
  transform: translate(-50%, -50%);
}

.circle-3 {
  top: 50%;
  left: 50%;
  width: 200px; /* Adjust size as needed */
  height: 200px; /* Adjust size as needed */
  border: 1px solid rgba(80,80,80, 0.3);
  transform: translate(-50%, -50%);
}

.circle-4 {
  top: 50%;
  left: 50%;
  width: 250px; /* Adjust size as needed */
  height: 250px; /* Adjust size as needed */
  border: 1px solid rgba(80,80,80, 0.2);
  transform: translate(-50%, -50%);
}

.fuse-section:hover .circle{
  animation: collapse-expand 2s ease forwards;
}


/* Keyframes for collapsing and expanding circles */
@keyframes collapse-expand {
  0% {
      transform: translate(-50%, -50%) scale(1); /* Original size */
      opacity: 1;
  }
  50% {
      transform: translate(-50%, -50%) scale(0); /* Collapse to center */
      opacity: 0.5; /* Optional fade effect */
  }
  100% {
      transform: translate(-50%, -50%) scale(1); /* Return to original size */
      opacity: 1;
  }
}


@keyframes dogBgMobile {
  0%{
    max-width: auto;
    max-height: auto;
    opacity: 1;
  }
  50%{
    max-width: 0%;
    max-height: 0%;
    opacity: 0
  }
  100%{
    max-width: auto;
    max-height: auto;
    opacity: 1;

  }
  
}

.eth img,
